import React from 'react'
import { NavLink } from 'react-router-dom'
import styled from 'styled-components'

const StyledSubmenu = styled.div`
text-align: right;
display: flex;
flex-direction: column;
	a{
		color: rgba(0,0,0,0.7);
		margin: 0 10px;
		text-decoration: none;
		font-size: 12px;
		padding: 6px 0;
		&::before{
			transition: 200ms ease;
			content: '...';
			opacity: 0;
		}
		&.active{
			color: black;
			&::before{
				opacity: 1;
			}
		}
	}

	@media only screen and (min-width: 679px) {
		padding: 10px 40px;
		display: block;

		a{
			padding: 0;
			font-size: 15px;
			&:hover{
				&::before{
					opacity: 1;
				}
			}
		}
	}
`


const SubMenu = () => (
	<StyledSubmenu>
		<NavLink to="/about/techstack" activeClassName="active">Technologies</NavLink>
		{/* <NavLink to="/about/qa" activeClassName="active">Q&A</NavLink> */}
		<NavLink to="/about/workflow" activeClassName="active">Workflow</NavLink>
	</StyledSubmenu>
)

export default SubMenu