import React from 'react'
import { Link, NavLink } from 'react-router-dom'
import styled from 'styled-components'

import Menu from './Menu'
import Backdrop from './Backdrop'

const StyledHeader = styled.header`
	display: flex;
	justify-content: space-between;
	align-items: center;

	.logo{
		margin: 20px;
		a{
			color: black;
			text-decoration: none;
			h1{
				font-weight: 300;
				.rn{
					font-weight: 600;
					position: relative;
					color: white;
					background: black;
					padding: 10px 4px 10px 0;
					letter-spacing: .7px;
					&::after{
						content: '';
						width: 5px;
						height: 5px;
						border-radius: 50%;
						// background: white;
						position: absolute;
						top: 12px;
						left: 16px;
						border: 1px solid white;
						
					}
				}
			}
		}
	}
	.hamburger{
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		width: 16px;
		height: 11px;
		position: fixed;
		right: 20px;
		top: 30px;
		.hamburgerLine{
			width: 100%;
			height: 1px;
			background: black;
		}

		@media only screen and (min-width: 680px) {
			display: none;
		}
	}
`


class Nav extends React.Component {
	state = {
		sideDrawer: false
	}

	handleSideMenu = () => {
		this.setState((prevState) => {
			return {
				sideDrawer: !prevState.sideDrawer
			}
		})
	}
	hideSideMenu = () => {
		this.setState({ sideDrawer: false })
	}

	render() {
		let showMenu;
		let backdrop;
		this.state.sideDrawer ? showMenu = "display" : showMenu = null;
		this.state.sideDrawer ? backdrop = <Backdrop hide={this.hideSideMenu} /> : backdrop = null;
		return (
			<StyledHeader>
				<div className="logo">
					<Link to="/">
						<h1><span className="ax">alex</span><span className="rn">razvan</span></h1>
					</Link>
				</div>

				<div onClick={this.handleSideMenu} className="hamburger">
					<div className="hamburgerLine"></div>
					<div className="hamburgerLine"></div>
					<div className="hamburgerLine"></div>
				</div>
				{backdrop}
				<Menu show={showMenu} hide={this.hideSideMenu} />
			</StyledHeader>
		)
	}
}

export default Nav