import React from 'react'
import data from '../../data/data.json'
import styled from 'styled-components'

const StyledDiv = styled.div`
	.announcement{
		width: 600px;
		margin: 40px auto;
		font-size: 15px;
		text-align: center;
		border: 1px solid black;
		padding: 20px;
		outline: 2px solid black;
		outline-offset: 3px;
	}

	.qa{
		padding: 15px 10%;
		border-bottom: 2px dashed rgba(0,0,0,0.1);
		margin: auto;

		h4{
			font-size: 12px;
			margin-bottom: 5px;
		}
		p{
			font-size: 12px;
		}
	}
`

const QA = () => (
	<StyledDiv>
		<div className="announcement">
			<h4>For the sake of efficiency, I've answered a couple of behavioral questions in advance.</h4>
		</div>
		{
			data.qa.map(item =>
				<div key={item.question} className="qa">
					<h4>{item.question}</h4>
					{item.answer.map(ans => (<p key={ans}>{ans}</p>))}
				</div>)
		}
	</StyledDiv>
)

export default QA