import React from 'react'
import styled from 'styled-components'

const StyledThumb = styled.li`
	position: relative;
	width: 60px;
	cursor: pointer;
	display: flex;
	justify-content: center;
	align-items: center;
	margin-top: 10px;

	&:hover span{
		opacity: 1;
	}
	span{
		transition: 100ms ease;
		position: absolute;
		opacity: 0;
		display: flex;
		justify-content: center;
		align-items: center;
		height: 100%;
		width: 100%;
		padding: 5px;
		font-size: 10px;
		background: white;
		font-weight: bold;
		white-space: nowrap;
	}
	img{
		max-width: 30px;
		max-height: 30px;
	}

	@media only screen and (min-width: 680px) {
		img{
			max-width: 50px;
			max-height: 30px;
		}
	}
`


const TechThumb = (props) => {
	return (
		<StyledThumb>
			<span>{props.thumb.name}</span>
			<img src={require('../../assets/logos' + props.thumb.logoImg)} alt="" />
		</StyledThumb>
	)
}

export default TechThumb