import React from 'react'
import { NavLink } from 'react-router-dom'
import styled from 'styled-components'

const StyledNav = styled.nav`
//mobile
position: fixed;
right: 0;
transform: translateX(230px);
top: 0;
width: 230px;
background: white;
height: 100vh;
z-index: 2;
display: flex;
flex-direction: column;
justify-content: center;
align-items: center;
list-style-type: none;
transition: 100ms ease-out;

a{
	text-decoration: none;
	color: black;
	font-size: 18px;
	margin: 20px 0;
	display: flex;
	text-transform: uppercase;
	letter-spacing: 2px;
	&::before{
		content: '{'
	}
	&::after{
		content: '}';
	}
	&::before, &::after{
		opacity: 0;
		margin: 0 10px;
		transition: 100ms ease;
	}
	&.active{
		&::before, &::after{
			opacity: 1;
		}
	}
}
&.display{
	transform: translateX(0);
}


//desktop
@media only screen and (min-width: 680px) {
	position: relative;
	height: 100%;
	width: initial;
	background: initial;
	flex-direction: row;
	transform: translateX(0);

	a{
		margin: 0 2rem;
		&:hover{
			&::before, &::after{
				opacity: 1;
			}
		}
	}
}
`

const Menu = (props) => (
	<StyledNav className={props.show}>
		<NavLink to='/about/techstack' activeClassName="active" onClick={props.hide}>
			<li>about</li>
		</NavLink>
		<NavLink to='/works' activeClassName="active" onClick={props.hide}>
			<li>works</li>
		</NavLink>
		<NavLink to='/contact' activeClassName="active" onClick={props.hide}>
			<li>contact</li>
		</NavLink>
	</StyledNav>
)

export default Menu