							<p>During the monthlong 10-12hours/day selection (sometimes 24hour coding challenges), candidates had to learn the basics of C and Bash, eventually getting to the point where one was able to build a <span className="emphasized">Sastantua Pyramid</span> and a <span className="emphasized">Sudoku solver</span>.</p>
